<template>
  <div>
    <h1>{{ msg }}</h1>
    <br />
    <button v-on:click="loginButton" id="login" class="btn btn-primary">Log in with Spotify</button>
    <div id="credits">
      <p>
        <b>Logo font:</b>
        <br />Streamster by Youssef Habchi
        <br />
        <br />
        <b>Vue components:</b>
        <br />
        <a href="https://deviavir.github.io/vue-bar/?ref=madewithvuejs.com">Vue Bars</a>
        <br />
        <a href="https://cinwell.com/vue-trend/">Vue Trend</a>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "PitchLanding",
  data: () => ({
    stateKey: "spotify_auth_state"
  }),
  props: {
    msg: String
  },
  methods: {
    loginButton: function() {
      this.login();
    },
    login: function() {
      const client_id = "183f77aa2d88476fb7e593ce69ef1471"; // Your client id

      //dev redirect uri
      //const redirect_uri = "http://localhost:8080/#/";
      // production redirect uri
      const redirect_uri = "https://sclowbird.github.io/PitchPerception/#/";

      const scope =
        "user-read-private playlist-read-private playlist-read-collaborative user-library-read user-read-email";
      const state = this.generateRandomString(16);

      //localStorage.setItem(this.stateKey, state);
      sessionStorage.setItem(this.stateKey, state);
      var url = "https://accounts.spotify.com/authorize";
      url += "?response_type=token";
      url += "&client_id=" + encodeURIComponent(client_id);
      url += "&scope=" + encodeURIComponent(scope);
      url += "&redirect_uri=" + encodeURIComponent(redirect_uri);
      url += "&state=" + encodeURIComponent(state);
      window.location = url;
    },

    generateRandomString: function(length) {
      var text = "";
      var possible =
        "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

      for (var i = 0; i < length; i++) {
        text += possible.charAt(Math.floor(Math.random() * possible.length));
      }
      return text;
    }
  }
};
</script>

